body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  box-sizing: border-box;
  cursor: default;
  color: #222; }

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  font-size: 0.875em;
  font-family: 'Source Code Pro', monospace, sans-serif;
  font-weight: 400;
  box-sizing: inherit; }

#root {
  width: 100vw;
  height: 100vh; }

.App {
  text-align: center;
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  width: 95vw;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
  background-color: ghostwhite;
  box-shadow: 0px 0px 15px 1px rgba(100, 100, 100, 0.3);
  margin: 0 auto; }

h1 {
  font-size: 5.9vw; }

h2 {
  font-size: 3vh; }

#section1 {
  color: #fff; }

.welcome {
  background-color: red; }
  .welcome img {
    height: 50vh;
    margin-bottom: 5vh; }

.fp-section {
  text-align: center; }

button {
  padding: 0.93em 1.87em;
  background: #35495e;
  border-radius: 5px;
  border-color: transparent;
  display: block;
  color: #fff;
  margin: 0 auto;
  cursor: pointer;
  font-size: 0.85em; }

.menuItem {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh; }

.item-detail {
  font-size: 4vh;
  color: #d3d3d3;
  padding: 4vh; }
  .item-detail__darkChoco {
    font-size: 5vh; }
  .item-detail__oolong {
    font-size: 3vh; }

.item-price {
  color: #d3d3d3; }
  .item-price h1 {
    font-size: 5vh;
    padding: 6vh;
    margin-bottom: 2vh; }
  .item-price p {
    font-size: 5vh;
    padding: 6vh; }
    .item-price p span {
      font-size: 3vh; }

.notification {
  font-size: 5vh;
  color: #d3d3d3;
  padding: 6vh; }
  .notification a {
    color: #d3d3d3; }

.usage {
  font-size: 5vh;
  color: #222;
  padding: 6vh;
  display: grid;
  grid-template-columns: 2fr 1fr 2fr;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center; }
  .usage__leftArrow img {
    width: 50%;
    -webkit-filter: opacity(0.5) drop-shadow(0 0 0 lightGray);
            filter: opacity(0.5) drop-shadow(0 0 0 lightGray); }

.slideHeader {
  font-size: 3vh;
  color: #d3d3d3;
  display: -webkit-flex;
  display: flex;
  padding: 3vh;
  position: absolute;
  top: 2px;
  background-color: rgba(10, 10, 10, 0.5); }

.fp-controlArrow {
  -webkit-user-select: none;
  /* webkit (safari, chrome) browsers */
  -moz-user-select: none;
  /* mozilla browsers */
  -khtml-user-select: none;
  /* webkit (konqueror) browsers */
  -ms-user-select: none;
  /* IE10+ */
  position: absolute;
  z-index: 4;
  top: 50%;
  cursor: pointer;
  width: 0;
  height: 0;
  border-style: solid;
  margin-top: -38px;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.fp-controlArrow.fp-prev {
  left: 1vw;
  width: 0;
  border-width: 9vw 7vw 9vw 0;
  border-color: transparent rgba(226, 223, 223, 0.55) transparent transparent; }

.fp-controlArrow.fp-next {
  right: 1vw;
  border-width: 9vw 0 9vw 7vw;
  border-color: transparent transparent transparent rgba(226, 223, 223, 0.55); }

#fp-nav ul li a span,
.fp-slidesNav ul li a span {
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
  background: transparent;
  box-sizing: border-box;
  box-shadow: inset 0 0 0 2px white;
  overflow: hidden; }
  #fp-nav ul li a span::after,
  .fp-slidesNav ul li a span::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    border-radius: 50%;
    left: 0;
    bottom: 0;
    background-color: #fff;
    box-shadow: 0 0 1px #fff;
    transition: height 0.3s; }

#fp-nav ul li a.active span,
.fp-slidesNav ul li a.active span,
#fp-nav ul li:hover a.active span,
.fp-slidesNav ul li:hover a.active span {
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
  background: transparent;
  box-sizing: border-box;
  box-shadow: inset 0 0 0 2px white; }
  #fp-nav ul li a.active span::after,
  .fp-slidesNav ul li a.active span::after,
  #fp-nav ul li:hover a.active span::after,
  .fp-slidesNav ul li:hover a.active span::after {
    height: 100%; }

